"use client"

import { useEffect, useState } from "react"
import {
  strengthLevelColors,
  type StrengthLevel,
} from "@/lib/strength-calculator"

interface AnimatedBodyProps {
  muscleStrengths: Record<string, StrengthLevel>
  gender: "male" | "female"
}

export function AnimatedBody({
  muscleStrengths,
  gender,
}: AnimatedBodyProps) {
  const [pulse, setPulse] = useState(false)

  // Simple breathing / pulse animation
  useEffect(() => {
    const interval = setInterval(() => {
      setPulse((p) => !p)
    }, 1200)
    return () => clearInterval(interval)
  }, [])

  const getColor = (muscle: string) =>
    strengthLevelColors[muscleStrengths[muscle] || "average"]

  const scale = pulse ? 1.03 : 1

  return (
    <div className="flex items-center justify-center w-full h-full bg-black">
      <svg
        viewBox="0 0 200 400"
        className="w-full h-full max-w-sm transition-transform duration-700 ease-in-out"
        style={{ transform: `scale(${scale})` }}
      >
        {/* HEAD */}
        <circle cx="100" cy="28" r="16" fill="#1f1f1f" />

        {/* NECK */}
        <rect x="92" y="44" width="16" height="12" rx="6" fill="#1f1f1f" />

        {/* SHOULDERS */}
        <circle cx="60" cy="70" r="14" fill={getColor("shoulder_left")} />
        <circle cx="140" cy="70" r="14" fill={getColor("shoulder_right")} />

        {/* CHEST / TORSO */}
        <rect
          x={gender === "female" ? 72 : 70}
          y="70"
          width={gender === "female" ? 56 : 60}
          height="85"
          rx="22"
          fill={getColor("chest_left")}
        />

        {/* ABS */}
        <rect
          x="84"
          y="100"
          width="32"
          height="45"
          rx="14"
          fill={getColor("abs")}
          opacity={0.9}
        />

        {/* ARMS */}
        <rect
          x="42"
          y="85"
          width="18"
          height="90"
          rx="9"
          fill={getColor("bicep_left")}
        />
        <rect
          x="140"
          y="85"
          width="18"
          height="90"
          rx="9"
          fill={getColor("bicep_right")}
        />

        {/* HIPS */}
        <rect
          x={gender === "female" ? 72 : 78}
          y="155"
          width={gender === "female" ? 56 : 44}
          height="26"
          rx="13"
          fill="#1f1f1f"
        />

        {/* LEGS */}
        <rect
          x="78"
          y="180"
          width="18"
          height="130"
          rx="9"
          fill={getColor("quad_left")}
        />
        <rect
          x="104"
          y="180"
          width="18"
          height="130"
          rx="9"
          fill={getColor("quad_right")}
        />

        {/* CALVES */}
        <rect
          x="78"
          y="300"
          width="18"
          height="70"
          rx="9"
          fill={getColor("calf_left")}
        />
        <rect
          x="104"
          y="300"
          width="18"
          height="70"
          rx="9"
          fill={getColor("calf_right")}
        />
      </svg>
    </div>
  )
}
